@import url(https://fonts.googleapis.com/css2?family=Pattaya&family=Poppins:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css?family=Lato:400,700);
/* Global Styles */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%
}

body {
  font-family: 'Poppins', sans-serif;
}

h1 {
  font-size: 2.6rem;
}

li,
button,
label,
input,
p {
  font-size: 2rem;
}

h2 {
  font-size: 4.8rem;
}

h3 {
  font-size: 3rem;
  font-weight: 400;
}

h4,
h5 {
  font-size: 2.8rem;
}

a {
  color: white;
  text-decoration: none;
}

.btn {
  padding: 2rem 6rem;
  background: #9003fc;
  color: white;
  font-size: 2rem;
  text-transform: uppercase;
}

.btn:hover {
  background: #121c27;
}

/* Nav Section */

.main-head {
  background: #7d6bf2;
  color: white;
}



nav {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  min-height: 10vh;
  padding: 0.5vh;

  width: 90%;
  margin-Left: 2.5%;
  width: 95%;
  padding-top: 0.8vh;


}

nav ul {
  display: flex;
  flex: 40% 1;
  justify-content: space-around;
  align-items: center;
  list-style: none;
}

#logo {
  flex: 40% 1;
  flex-direction: row;
  font-weight: 400;
}

/* Hero Section */

.hero {
  background: linear-gradient(rgba(0, 0, 0, 0.5), transparent), url(/backend/static/media/amita-back.2849425a.jpg);
  background-repeat: no-repeat;
  
  background-position: center;
  min-height: 55vh;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.hero h3 {
  padding: 3rem;
}

.hero .wrapper {
  width: 90%;
  margin: auto;
}

/* Featured Books and Books Section */

.featured-head {
  background: white;
  padding: 2rem;
  color: black;
  
  display: flex;
  justify-content: center;
  align-items: center;
}

.books {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  text-align: center;
  width: 90%;
  margin: auto;
}

.book {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.1), 0px 20px 20px rgba(0, 0, 0, 0.1);
  width: 22rem;
  height: 28rem;
  margin: 3rem 4rem;
  border-top: 1px solid #efefef;
  background: floralwhite;
}

.featured-list {
  justify-content: space-evenly;
}

.featured-book {
  margin: 20px 2%;
}

.book-image {
  flex: 3 1 100rem;
  padding: 2rem 0rem;
}

.book-link {
  flex: 1 1 100rem;
  background: #55606c;
  font-size: 1.7rem;
  width: 100%;
  height: 100%;
}

.book-link:hover {
  background: #343c43;
}

.book img {
  min-height: 10vh;
  height: 21vh;
}

/* Book Details Section */

.book-details {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 90%;
  margin: auto;
  height: 80vh;
  text-align: center
}

.detail-image {
  text-align: center;
}

.detail-description {
  display: flex;
  flex: 1 1 50rem;
  flex-direction: column;
  justify-content: space-evenly;
  height: 75%;
  padding: 0 5rem;
}

/* Admin Section */

.admin-wrapper {
  width: 90%;
  margin-left: 5%;
  text-align: center;
  margin-bottom: 2% ;
}

.form-header {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  padding: 3rem 0;
}

.form-wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  border: 1px #c7c7c7 solid;
  padding: 2rem;
}

.form-image {
  flex: 1 1 50rem;
}

.image-preview {
  width: 100%;
}

.form-fields {
  flex: 3 1 50rem;
  text-align: left;
}

.form-fields input {
  width: 100%;
  height: 4rem;
  font-size: 2rem;
}

.form-fields textarea {
  width: 100%;
  font-size: 2rem;
}

.form-wrapper label {
  margin: 0rem 2rem;
  text-align: left;
}

.title-form,
.description-form,
.author-form,
.price-form,
.submit-form,
.featured-form {
  padding: 0.7rem;
}

.form-fields .featured-checkbox {
  width: 2rem;
  position: relative;
  top: 1.4rem;
}

.submit-form {
  width: 100%;
}

/* Cart Section */

.cart {
  width: 90%;
  margin: auto;
  text-align: center;
}

.cart-wrapper {
  display: flex;
  flex-direction: column;
}

.cart-item {
  margin: 2rem 0rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}

.cart-item .image {
  flex: 1 1;
}

.cart-item img {
  width: 100%;
}

.cart-item .details {
  flex: 2 1 10rem;
}

.cart-item .amount {
  flex: 1 1 10rem;
}

.cart-empty {
  position: absolute;
  top: 48%;
  left: 40%;
}

/* Checkout Section */

.checkout-wrapper {
  width: 60%;
  margin: auto;
  text-align: center;
}

.checkout-form {
  display: flex;
  flex-direction: column;
  margin: 3rem 0;
}

.stripe-section {
  margin-top: 2rem;
}

@media screen and (max-width: 933px) {
  html {
    font-size: 62.5%;
  }

  nav {
    text-align: center;
  }

  #logo {
    padding: 1rem;
  }

  .book-details {
    margin-top: 4rem;
  }
}



/* Loading payment status*/

.loading{
  font-size:100px; /* Default value */
  --duration:1s; /* Default value */
  --num-dot:10; /* Default value */
  --color-dot:rgb(100,100,255); /* Default value */
  --box-color:rgb(50,50,50); /* Default value */
}
.loading > *{
  position:absolute;
  width:100%;
  height:100%;
  top:0;
  left:0;
  -webkit-transform:rotate(calc((360deg / var(--num-dot) /* Number of child */) * var(--index)));
          transform:rotate(calc((360deg / var(--num-dot) /* Number of child */) * var(--index)));
}
.loading > *::before{
  position:absolute;
  content:'';
  top:.15em;
  left:.45em;
  width:.1em;
  height:.1em;
  -webkit-transform:scale(0);
          transform:scale(0); /* First state is scale from 0 */
  border-radius:50%;
  background-color:var(--color-dot);
  animation:scaleTo1 var(--duration) ease-in-out calc(var(--duration) / var(--num-dot) * var(--index)) infinite reverse;
}
@-webkit-keyframes scaleTo1 {
  0% {
    -webkit-transform:scale(0);
            transform:scale(0);
  }
  100% {
    -webkit-transform:scale(1);
            transform:scale(1);
  }
}
@keyframes scaleTo1 {
  0% {
    -webkit-transform:scale(0);
            transform:scale(0);
  }
  100% {
    -webkit-transform:scale(1);
            transform:scale(1);
  }
}


/*  Box style only  */
.loading{
  width:1em;
  aspect-ratio:1;
  border-radius:.2em;
  background-image:linear-gradient(135deg, rgb(200,200,200), rgb(0,0,0));
  box-shadow:3px 3px 5px rgba(0,0,0,.8);
  position:relative;
  font-size:200px;--duration:2s;--num-dot:10;
}
/* Box style only */
.loading::before{
  content:'';
  position:absolute;
  top:1px;
  left:1px;
  right:1px;
  bottom:1px;
  border-radius:.2em;
  background-color:var(--box-color);
}

/* Background style only */
.bodyloading{
  width:100vw;
  height:90vh;
  margin:0;
  background-color:rgb(150,150,200,0.5);
  display:grid;
  place-items:center;
}

.index0
{
  --index:0
}
.index1
{
  --index:1
}
.index2
{
  --index:2
}
.index3
{
  --index:3
}
.index4
{
  --index:4
}
.index5
{
  --index:5
}
.index6
{
  --index:6
}
.index7
{
  --index:7
}
.index8
{
  --index:8
}
.index9
{
  --index:9
}
